
if (typeof FastBoot === 'undefined') {
  if (!runningTests) {
    require('shiny-components/src/main')['default'].create({"name":"shiny-components","version":"0.0.0+17028041"});
  }
}

define('~fastboot/app-factory', ['shiny-components/src/main', 'shiny-components/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});

