    "main.js": "import Application from \"@ember/application\";\nimport Resolver from \"./resolver\";\nimport loadInitializers from \"ember-load-initializers\";\nimport config from \"../config/environment\";\n\nconst App = Application.extend({\n  modulePrefix: config.modulePrefix,\n  podModulePrefix: config.podModulePrefix,\n  Resolver\n});\n\nloadInitializers(App, config.modulePrefix + \"/src/init\");\n\n/*\n * This line is added to support initializers in the `app/` directory\n */\nloadInitializers(App, config.modulePrefix);\n\nexport default App;\n",
    "resolver.js": "import Resolver from 'ember-resolver/resolvers/fallback';\nimport buildResolverConfig from 'ember-resolver/ember-config';\nimport config from '../config/environment';\n\nlet moduleConfig = buildResolverConfig(config.modulePrefix);\n/*\n * If your application has custom types and collections, modify moduleConfig here\n * to add support for them.\n */\n\nObject.assign(moduleConfig.types, {\n\t'component-manager': {definitiveCollection: 'main'}\n});\n\nmoduleConfig.collections.main.types.push('component-manager');\n\nexport default Resolver.extend({\n  config: moduleConfig\n});\n",
    "router.js": "import EmberRouter from \"@ember/routing/router\";\nimport config from \"../config/environment\";\n\nconst Router = EmberRouter.extend({\n  location: config.locationType,\n  rootURL: config.rootURL\n});\n\nRouter.map(function () {\n  this.route('best-practices', function () {\n    this.route('sparkles');\n    this.route('singel');\n    this.route('component-classification');\n    this.route('naming-conventions');\n  });\n\n  this.route('patterns', function () {\n    this.route('component-types', function () {\n      this.route('presentational-components');\n      this.route('contextual-components');\n      this.route('container-components');\n      this.route('high-order-components');\n    });\n\n    this.route('composites', function () {\n      this.route('inheritance');\n      this.route('composition');\n    });\n\n    this.route('dom-api');\n  });\n});\n\nexport default Router;\n",
